import "core-js/modules/web.dom-collections.for-each.js";
import Carousel from "shared/ts/components/carousel";
import SlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/slide-carousel";
import FadeCarousel from "shared/ts/components/fade-carousel";
import FadeSlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/fade-slide-carousel";
import VimeoCarousel from "Shared/ts/components/vimeo-carousel";
import VimeoSlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/vimeo-slide-carousel";
import ResponsiveCarousel from "Shared/ts/components/responsive-carousel";
import ResponsiveSlideCarouselAdapter from "Shared/ts/api/carousel/slide/adapters/responsive-slide-carousel";
import MediaQuery from "Shared/ts/observers/media-query";
import { enumerateElements } from "shared/ts/utils/html";
import { observer } from "shared/ts/observers/intersection";
import VimeoManager from "shared/ts/utils/vimeo-manager";
var vimeoManager = new VimeoManager();
enumerateElements(document.querySelectorAll("[data-vimeo-carousel-id]")).forEach(function (placeholder) {
  var id = placeholder.getAttribute("data-vimeo-carousel-id");
  vimeoManager.createoEmbedById(id).then(function (response) {
    try {
      var data = JSON.parse(response.response);
      placeholder.setAttribute("data-src-img", data.thumbnail_url);
    } catch (error) {
      console.warn(error);
    }
  });
});

var preloadSlideImageByIndex = function preloadSlideImageByIndex(container, index) {
  if ("app" in window && window.app.require(["lazy"])) {
    var slide = container.querySelectorAll(".slide__item").item(index);
    window.app.lazy(slide);
  }
};

observer(".slide--vimeo-carousel", {
  inRange: function inRange(element) {
    var carousel = new VimeoCarousel(new VimeoSlideCarouselAdapter(element));
    carousel.autoplay();
    carousel.enableThumbnailControls();
    carousel.on("rotation", function (currentIndex, prevIndex, nextIndex) {
      preloadSlideImageByIndex(element, currentIndex);
      preloadSlideImageByIndex(element, nextIndex);
    });
  },
  options: {
    threshold: [0.5]
  }
});

var generateRandomIntegerInRange = function generateRandomIntegerInRange(min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

observer(".slide--fade", {
  inRange: function inRange(element) {
    var initialDelay = generateRandomIntegerInRange(3000, 5000);
    var carousel = new FadeCarousel(new FadeSlideCarouselAdapter(element));
    carousel.setAttributes({
      delay: initialDelay
    });
    carousel.autoplay();
    carousel.on("rotation", function (currentIndex) {
      var delay = currentIndex === 0 ? initialDelay : initialDelay * 1.5;
      carousel.setAttributes({
        delay: delay
      });
    });
  }
});
observer(".slide--review", {
  inRange: function inRange(element) {
    var carousel = new ResponsiveCarousel(new ResponsiveSlideCarouselAdapter(element));
    carousel.enablePrevNextControls();
    new MediaQuery("(min-width: 1201px)").inbound(function (event) {
      carousel.setAttributes({
        steps: 3
      });
    });
    new MediaQuery("(min-width: 1001px) and (max-width: 1200px)").inbound(function (event) {
      carousel.setAttributes({
        steps: 3
      });
    });
    new MediaQuery("(min-width: 801px) and (max-width: 1000px)").inbound(function (event) {
      carousel.setAttributes({
        steps: 2
      });
    });
    new MediaQuery("(max-width: 800px)").inbound(function (event) {
      carousel.setAttributes({
        steps: 1
      });
    });
  }
});
observer(".product--single .slide--product:not(.slide--fade)", {
  inRange: function inRange(element) {
    var carousel = new Carousel(new SlideCarouselAdapter(element));
    carousel.on("rotation", function (currentIndex) {
      return preloadSlideImageByIndex(element, currentIndex);
    });
  }
});